*
{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;

    // -webkit-user-select: none;

    outline: none;

    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: transparent;
}


body
{
    margin: 0px;
    font-family: STHeitiTC, "Helvetica Neue", Helvetica, sans-serif;
    font-size: 75%;
    color: #333;
}

input,select,textarea
{
    font-family: STHeitiTC, "Helvetica Neue", Helvetica, sans-serif;
    font-size: 87.5%;
    outline: none;
    &:hover,
    &:focus
    {
        outline: none;
    }
}

ul,li,dt,dl,dd
{
    padding: 0px;
    margin: 0px;
    list-style-type: none;
}

img
{
    max-width: 100%;
    vertical-align: top;
}
cite
{
    font-style: normal;
}


a
{
    color: inherit;
    &:link,
    &:hover,
    &:visited,
    &:active,
    &:focus
    {
        text-decoration: none;
    }
}
pre 
{
    padding: 0px;
    margin: 0px;
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-word;
    background-color: transparent;
    border: none;
}


