
@font-face {
    font-family: iconfont;
    font-style: normal;
    font-weight: normal;
    src: url("../fonts/iconfont.ttf");
}

@mixin iconfont 
{
    font-family:"iconfont" !important;
    font-size:16px;
    font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}

.iconfont
{
    @include iconfont();
}

.icon-tabbar-mobile:before { content: "\e634"; }

.icon-tabbar-transfer:before { content: "\e6e6"; }

.icon-tabbar-account:before { content: "\e624"; }

.icon-tabbar-home:before { content: "\e632"; }




